import React from 'react'
import { CustomButton } from '../globalComponents'


const CommonApproveRejectModal = (props) => {
    const { title, onApproveReject, isShow, isLoading, btnTextLeft, btnTextRight, setComment, id, approveFor, comment } = props
    return (
        
        <>
            <div className="modal fade correctionAprovel" id="mrfApproval" style={{ display: isShow ? "unset !important" : "none !important", opacity: isShow ? "unset" : "0" }}>
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content rounded-0 border-0">
                        <div className="modal-header border-0">
                            <h1 className="modal-title fs-5 fw-bold text-primary">{title}</h1>
                            <CustomButton
                                classNames={"btn-close"}
                                handleClick={() => onApproveReject('')}
                            />
                            {/* <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button> */}
                        </div>
                        <div className="modal-body pt-0">
                            <div className="d-flex align-items-center justify-content-between mb-4">
                                {approveFor && <strong className="fs-7 text-light text-end">Applied For<span
                                    className="text-primary fw-bolder d-block">{approveFor}</span></strong>}
                            </div>
                            <span className="text-primary fw-semibold mb-2 d-block fs-8">Enter Reason</span>
                            <div className="form-group mb-3">
                                <textarea className="form-control" placeholder="Enter Reason"
                                    value={comment}
                                    style={{ height: "70px" }}
                                    onChange={(e) => setComment(e.target.value)}
                                ></textarea>
                            </div>
                            <div className="my-3 d-flex justify-content-center gap-3">
                                <CustomButton
                                    classNames={"btn btn-danger"}
                                    isLoading={isLoading}
                                    text={btnTextLeft || "Reject"}
                                    handleClick={() => onApproveReject("reject")}
                                />
                                <CustomButton
                                    classNames={"btn btn-success"}
                                    isLoading={isLoading}
                                    text={btnTextRight || "Approve"}
                                    handleClick={() => onApproveReject("approve")}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {isShow && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CommonApproveRejectModal
